body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style: none;
  list-style-position: inside;
  padding-left: 0;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  /* background-image: url("https://www.juliusbaer.com/fileadmin/_processed_/0/b/csm_JB_IMG_Places_353_BGL1_RGB_LO_721ab00129.jpg"); */
  /* background-size: cover; */
}

.App-header {
  background-color: #141e55;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;

  overflow: hidden;
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */

  padding-bottom: 10px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, .5);
}

.App-header h1 {
margin: 2px;
}

.App-header p {
  margin: 0px;
}  

.App-content {
  margin-top: 90px;
}

.AppList {
    text-align: center;
}


li.AppStore {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 10px;
    margin-bottom: 15px;
}

.AppStore img {
    width: 100px;
    height: 100px;
    border-radius: 18%;
}

.AppStore p {
    margin-top: 0px;
    margin-bottom: 10px;
}
.AppItems {
    padding: 10px;
}
.AppItem {
    padding: 5px;
}
/* .DeviceList {
    text-align: center;
} */

.DeviceItem {
    width: 80%;
    border-bottom: 1px solid black;
    padding: 10px;
    text-align: left;
    margin: auto;
}

.PlatformIcon {
    float: left;
    padding-right: 10px;    
  /* width: 10%; */
}


.DeviceInfo {
    float: left;
    margin: auto;
  
  /* width: 90%; */
}

.DeviceInfo p {
    margin: 0px;
}

.DeviceItem:after {
    content: "";
    display: table;
    clear: both;
  }

.OSStatistics {
    text-align: center;
}

.PieChart {
    width: 30%;
    margin: auto;
}

.Table { 
    margin: 0px auto; 
    margin-top: 10px;
}

table, th, td {
    border: 1px solid white;
    border-collapse: collapse;
  }

  th, td {
    padding: 15px;
  }
